export const COUNTER_INCREMENT = 'COUNTER_INCREMENT'
export const COUNTER_DOUBLE_ASYNC = 'COUNTER_DOUBLE_ASYNC'
export const SET_ATTRIBUTE = 'SET_ATTRIBUTE'
export const PLAY_SOUND = 'PLAY_SOUND'

export function playSound (payload) {
  return {
    type    : PLAY_SOUND,
    payload
  }
}
export function increment (payload) {
  return {
    type    : COUNTER_INCREMENT,
    payload
  }
}
export function setAttribute (payload) {
  return {
    type    : SET_ATTRIBUTE,
    payload
  }
}

export const doubleAsync = () => {
  return (dispatch, getState) => {
    return new Promise((resolve) => {
      setTimeout(() => {
        dispatch({
          type    : COUNTER_DOUBLE_ASYNC,
          payload : getState().counter
        })
        resolve()
      }, 200)
    })
  }
}

export const actions = {
  increment,
  doubleAsync
}

// ------------------------------------
// Action Handlers
// ------------------------------------
const ACTION_HANDLERS = {
  [COUNTER_INCREMENT]    : (state, action) => {
    state.counter = state.counter + action.payload;
    return {...state}
  },
  [PLAY_SOUND]    : (state, action) => {
    var audio = new Audio(action.payload);
    audio.play();
    return {...state}
  },
  [SET_ATTRIBUTE]    : (state, action) => {
    if(action.payload.attribute.indexOf(".") !== -1){
      state[action.payload.attribute.split(".")[0]][action.payload.attribute.split(".")[1]] = action.payload.value;
    }else{
      state[action.payload.attribute] = action.payload.value;
    }
    return {...state}
  },
  [COUNTER_DOUBLE_ASYNC] : (state, action) => state.counter * 2
}

