import { connect } from 'react-redux'
import * as module from '../modules/sightWordsListModule'
import sightWordsListComponent from '../components/sightWordsListComponent'

const mapDispatchToProps = {
  setAttribute : (payload) => module.setAttribute(payload),
  playSound : (payload) => module.playSound(payload),
  increment : () => module.increment(1)
}

const mapStateToProps = (state) => ({
  sightwordslist : state.sightwordslist
})

export default connect(mapStateToProps, mapDispatchToProps)(sightWordsListComponent)



